import React, { Component } from "react";
import { BootstrapTable, TableHeaderColumn } from "react-bootstrap-table";
import "react-bootstrap-table/dist/react-bootstrap-table.min.css";
class ImageAllocationGrid extends Component {
  render() {
    const options = {
      noDataText: "There is no video allocation."
    };
    return (
      <BootstrapTable
        ref="table"
        data={this.props.videoAllocation}
        hover={true}
        options={options}
      >
        <TableHeaderColumn dataField="Id" headerAlign="left" isKey hidden>
          Id
        </TableHeaderColumn>
        <TableHeaderColumn dataField="VideoName" headerAlign="left" width="30">
          Video Name
        </TableHeaderColumn>
        <TableHeaderColumn
          dataField="LanguageName"
          headerAlign="left"
          width="30"
        >
          Language
        </TableHeaderColumn>
        <TableHeaderColumn
          dataField="FilePath"
          headerAlign="left"
          width="60"
          dataFormat={this.props.showVideo}
        >
          Video
        </TableHeaderColumn>
        <TableHeaderColumn
          dataField="Delete"
          headerAlign="left"
          width="30"
          dataFormat={this.props.onDelete}
        >
          Delete
        </TableHeaderColumn>
      </BootstrapTable>
    );
  }
}

export default ImageAllocationGrid;



