import React, { Component } from "react";
import { connect } from "react-redux";
import * as actions from "../../store/actions";
import CardLayout from "../../components/Cards/CardLayout";
import { FormGroup, Col, Button, Label } from "reactstrap";
import Loader from "../../components/Loader/Loader";
import _ from "lodash";
import { Async } from "react-select";
import "react-select/dist/react-select.css";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Toaster from "../../constants/Toaster";
import AppConfig from "../../constants/AppConfig";
import VideoPlayer from "../../components/VideoPlayer/VideoPlayer";
import DropdownSelect from "../../components/InputElement/Dropdown";
import NotificationModal from "./NotificationModal";

class VideoAllocationForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      updateFlag: false,
      videoAllocation: {
        Id: "",
        CropId: "",
        StepId: "",
        MaterialId: "",
        VideoId: "",
        LangId: "",
        LangIdRequired: false,
        videoRequired: false,
        Active: true,
        CreatedBy: "",
        CreatedOn: ""
      },
      cardTitle: "",
      videoCategory: "",
      videoValue: "",
      renderURL: "",
      error: "",
      notificationFlag: false
    };
  }
  componentDidMount() {
    let videoCategory = this.props.match.params.videoCategory;
    let videoAllocation = { ...this.state.videoAllocation };
    let compRef = this;
    setTimeout(() => {
      let id = compRef.props.match.params.id;
      let videoCategory = compRef.props.match.params.videoCategory;
      if (videoCategory === "crop") {
        videoAllocation.CropId = id;
      } else if (videoCategory === "cropStep") {
        videoAllocation.StepId = id;
      } else if (videoCategory === "cropMaterial") {
        videoAllocation.MaterialId = id;
      } else {
        compRef.goBack();
      }
      compRef.setState({
        loading: false,
        videoCategory: videoCategory,
        videoAllocation: videoAllocation
      });
    }, 1000);
  }
  goBack() {
    this.props.history.goBack();
  }
  handleVideoChange = videoSelected => {
    let videoAllocation = { ...this.state.videoAllocation };
    if (videoSelected !== null) {
      videoAllocation.VideoId = videoSelected.value;
      videoAllocation.videoRequired = false;
      let videoValue = videoSelected;
      let renderURL = `${AppConfig.serverURL}/${videoSelected.FilePath}`;
      this.setState({
        videoAllocation,
        renderURL,
        videoValue
      });
    } else {
      videoAllocation.VideoId = "";
      this.setState({ videoAllocation, renderURL: "", videoValue: "" });
    }
  };
  handleLanguageChange(languageValue) {
    let videoAllocation = { ...this.state.videoAllocation };
    videoAllocation.LangId = languageValue;
    videoAllocation.LangIdRequired = false;
    this.setState({ videoAllocation });
  }
  onSubmit() {
    let message = "";
    let compRef = this;
    let videoAllocation = { ...this.state.videoAllocation };
    let videoCategory = this.state.videoCategory;
    videoAllocation.CreatedBy = localStorage.getItem("user");
    videoAllocation.CreatedOn = new Date();
    if (videoAllocation.VideoId && videoAllocation.LangId) {
      if (videoCategory === "crop") {
        let cropVideoAllocation = _.pick(videoAllocation, [
          "CropId",
          "LangId",
          "VideoId",
          "CreatedBy",
          "CreatedOn",
          "Active"
        ]);
        this.props.postCropVideoAllocation(cropVideoAllocation);
      } else if (videoCategory === "cropStep") {
        let cropStepVideoAllocation = _.pick(videoAllocation, [
          "StepId",
          "LangId",
          "VideoId",
          "CreatedBy",
          "CreatedOn",
          "Active"
        ]);
        this.props.postCropStepVideoAllocation(cropStepVideoAllocation);
      } else {
        //cropMaterialVideo Allocation
      }
      this.setState({ loading: true });
      setTimeout(() => {
        let error = "";
        if (videoCategory === "crop") {
          error = compRef.props.cropVideoError;
        } else if (videoCategory === "cropStep") {
          error = compRef.props.cropStepVideoError;
        } else {
          //error = compRef.props.cropVideoError;
        }
        error
          ? (message = error)
          : (message = "Video allocation created successfully");
        compRef.setState({ loading: false });
        Toaster.Toaster(message, error);
        setTimeout(() => {
          if (!error) {
            compRef.onReset();
            compRef.goBack();
            //compRef.onNotificationToggle();
          }
        }, 2000);
      }, 2000);
    } else {
      if (!videoAllocation.VideoPath) {
        videoAllocation.videoRequired = true;
      }
      if (!videoAllocation.LangId) {
        videoAllocation.LangIdRequired = true;
      }
      this.setState({ videoAllocation });
    }
  }
  onReset() {
    let videoAllocation = { ...this.state.videoAllocation };
    videoAllocation.videoRequired = false;
    videoAllocation.FilePath = "";
    videoAllocation.LangId = "";
    videoAllocation.VideoId = "";
    (videoAllocation.LangIdRequired = false),
      this.setState({
        videoAllocation,
        videoValue: "",
        renderURL: "",
        error: ""
      });
  }
  onNotificationToggle() {
    this.setState({
      notificationFlag: !this.state.notificationFlag
    });
  }
  render() {
    const options = this.props.videoOptions;
    const getOptions = (input, callback) => {
      let filterOpt = [];
      if (input) {
        filterOpt = _.filter(options, function(video) {
          if (
            _.includes(video.label.toLowerCase(), input.toLowerCase()) ||
            video.label.toLowerCase() == input.toLowerCase()
          ) {
            return video;
          }
        });
      }
      setTimeout(() => {
        callback(null, {
          options: filterOpt,
          complete: true
        });
      }, 500);
    };
    return this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <CardLayout
        name="Video Allocation Form"
        navigation={true}
        navigationRoute={this}
        onClick={this.goBack.bind(this)}
      >
        <div className="div-padding">
          <FormGroup row>
            <Col xs="12" md="6">
              <FormGroup row>
                <Col xs="12" md="10">
                  <Label>Video</Label>
                  <Async
                    name="form-field-name"
                    cacheOptions
                    value={this.state.videoValue}
                    defaultOptions
                    onChange={this.handleVideoChange.bind(this)}
                    loadOptions={getOptions}
                  />
                  {this.state.videoAllocation.videoRequired ? (
                    <div className="help-block">*Video is required</div>
                  ) : null}
                </Col>
              </FormGroup>
              <FormGroup row>
                <Col xs="12" md="10">
                  <Label>Language</Label>
                  <DropdownSelect
                    name="Language"
                    placeholder="Select language "
                    options={this.props.languagesList}
                    value={this.state.videoAllocation.LangId}
                    required={this.state.videoAllocation.LangIdRequired}
                    onChange={this.handleLanguageChange.bind(this)}
                    clear={true}
                  />
                </Col>
              </FormGroup>
              <FormGroup row>
                <Col xs="12" md="2">
                  <Button
                    className="theme-positive-btn"
                    onClick={this.onSubmit.bind(this)}
                  >
                    Create
                  </Button>
                </Col>
                <Col md="2">
                  <Button
                    className="theme-reset-btn"
                    onClick={this.onReset.bind(this)}
                  >
                    Reset
                  </Button>
                </Col>
              </FormGroup>
            </Col>
            {this.state.renderURL ? (
              <Col md="6">
                <Col xs="12" md="10">
                  <VideoPlayer
                    autoPlay={false}
                    source={this.state.renderURL}
                    mute={true}
                  />
                </Col>
              </Col>
            ) : null}
          </FormGroup>
        </div>
        <ToastContainer autoClose={1000} />
        <NotificationModal
          active={true}
          category="video"
          notificationContext={this.props.match.params.videoCategory}
          contextId={parseInt(this.props.match.params.id)}
          openFlag={this.state.notificationFlag}
          toggleFunction={this.onNotificationToggle.bind(this)}
          shouldGoBack={true}
          {...this.props}
        />
      </CardLayout>
    );
  }
}
export const mapStateToProps = state => {
  return {
    videoOptions: state.mediaReducer.videoOptions,
    videoFiles: state.mediaReducer.videoFiles,
    cropStepError: state.cropsReducer.cropStepError,
    activeCropSteps: state.cropsReducer.activeCropSteps,
    languagesList: state.languagesReducer.languagesList,
    activeCrops: state.cropsReducer.activeCrops,
    activeCropMaterial: state.cropsReducer.activeCropMaterial,
    cropVideoError: state.cropsReducer.cropVideoError,
    cropStepVideoError: state.cropsReducer.cropStepVideoError
  };
};
export const mapDispatchToProps = dispatch => {
  return {
    getCropsList: () => dispatch(actions.getCropsList()),
    getCropStepsMaterial: () => dispatch(actions.getCropStepsMaterial()),
    getCropSteps: () => dispatch(actions.getCropSteps()),
    postCropVideoAllocation: cropVideo =>
      dispatch(actions.postCropVideoAllocation(cropVideo)),
    postCropStepVideoAllocation: cropStepVideo =>
      dispatch(actions.postCropStepVideoAllocation(cropStepVideo))
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(
  VideoAllocationForm
);



